<template lang="pug">
.filters
  .row-center
    .name.mr-r-4 屬性篩選
    .additional
      select.mr-r-4(v-model="cardType", @change="changeType")
        option(v-for="opt in cardTypes", :value="opt.key") {{ opt.name }}
      select.mr-r-4(
        v-if="cardProperties[cardType] !== undefined"
        v-model="cardProperty"
        @change="changeProperty")
        option(value="") 不限
        option(v-for="opt in cardProperties[cardType]", :value="opt") {{ opt }}
  .row-center.mr-t-4
    .name.mr-r-4 關鍵字
    input.mr-r-4(v-model.trim="keyword")
  .row-center.mr-t-4.add-row
    select.series.mr-r-4(v-if="keyword === ''", v-model="selectedSeries", @change="changeSeries")
      option(v-for="opt in series", :value="opt") {{ opt }}
    select.card.mr-r-4(v-model="selectedCard")
      option(v-for="show in showList", :value="show")
        template(v-if="keyword !== ''") {{show.series}}-
        template(v-if="show.type !== 'energy'") {{paddingToThree(show.id)}}-
        template {{show.name}}
    .mr-r-4 x
    select.num.mr-r-4
      option(v-for="n in 15", :value="n", v-if="n <= 4 || (selectedCard !== undefined && selectedCard.type === 'energy')"
        v-model="num") {{ n }}
    text-button(@click="addCard") 新增
</template>

<script>
import { pokemonProperties, trainerProperties } from '@/name/properties';
import { paddingToThree } from '@/utils/format.js';
import TextButton from '@/components/TextButton';
import name from '@/name';

export default {
  components: {
    TextButton,
  },
  watch: {
    keyword(newValue, oldValue) {
      if (oldValue !== '' && newValue === '') {
        this.selectedSeries = this.series[0];
      }
      this.doSearch();
    },
  },
  data() {
    return {
      mode: 'property',
      cardTypes: [
        { key: '', name: '不限' },
        { key: 'pokemon', name: '寶可夢' },
        { key: 'trainer', name: '訓練家卡' },
        { key: 'energy', name: '能量' },
      ],
      cardType: '',
      cardProperties: {
        pokemon: pokemonProperties,
        trainer: trainerProperties,
      },
      cardProperty: '',
      series: Object.keys(name).filter((x) => typeof name[x] === 'object'),
      keyword: '',
      filteredList: [],
      selectedSeries: '',
      selectedCard: undefined,
      num: 1,
    };
  },
  computed: {
    showList() {
      if (this.keyword === '') {
        return this.filteredList.filter(
          (x) => x.series === this.selectedSeries
        );
      }
      return this.filteredList;
    },
  },
  methods: {
    changeSeries() {
      const that = this;
      that.$nextTick(() => {
        that.selectedCard = that.showList[0];
      });
    },
    paddingToThree,
    addCard() {
      this.$emit('add', {
        selected: this.selectedCard,
        num: this.num,
      });
    },
    resetProperties() {
      this.cardProperty = '';
    },
    changeProperty() {
      this.doSearch();
    },
    changeType() {
      this.resetProperties();
      this.doSearch();
    },
    doSearch() {
      const that = this;
      that.filteredList = name.search(
        that.cardType,
        that.cardProperty,
        that.keyword
      );
      that.$nextTick(() => {
        that.selectedCard = that.showList[0];
      });
    },
  },
  mounted() {
    const that = this;
    that.doSearch();
    that.selectedSeries = that.series[0];
    that.$nextTick(() => {
      that.selectedCard = that.showList[0];
    });
  },
};
</script>

<style lang="scss" scoped>
.row-center {
  display: flex;
  align-items: center;
}

.mr-r-4 {
  margin-right: 4px;
}
.mr-t-4 {
  margin-top: 4px;
}

.mr-r-12 {
  margin-right: 12px;
}

.filters {
  line-height: 32px;
  label {
    text-shadow: 0 0.1em 0.1em rgba(0, 0, 0, 0.2);
    display: inline-block;
    cursor: pointer;

    .radio {
      border-radius: 1.6em;
      box-shadow: 0 0 0 0.1em currentColor inset,
        0 0.1em 0.1em rgba(0, 0, 0, 0.2), 0 0.3em 0.2em rgba(0, 0, 0, 0.2) inset;
      margin-right: 4px;
      width: 1em;
      height: 1em;
      display: inline-flex;
      align-items: center;
      justify-content: center;
      .inside {
        width: 0.4em;
        height: 0.4em;
        border-radius: 0.4em;
        background-color: black;
        transition: opacity 0.2s ease-in-out;
        opacity: 0.01;
      }
    }
    .name {
      color: black;
    }
  }
}

select {
  padding: 2px;
}

input {
  padding: 2px;
}

.add-row {
  .series {
    flex: 0 0 auto;
  }
  .card {
    flex: 1;
    max-width: 200px;
  }
  .num {
    flex: 0 0 auto;
  }
}
</style>
