<template lang="pug">
.text-button(@click="click")
  slot
</template>

<script>
export default {
  methods: {
    click() {
      this.$emit('click')
    }
  }
}
</script>

<style lang="scss" scoped>
.text-button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px;

  border: 1px solid black;
  height: 2em;

  user-select: none;
  cursor: pointer;
  &:hover {
    background: rgba(0, 0, 0, 0.05);
  }
}
</style>