<template lang="pug">
.card-img
  img(:src="src", :style="imgStyle")
</template>

<script>
import { paddingToThree } from '../utils/format.js';

export default {
  props: {
    series: {
      type: String,
      required: true,
    },
    id: {
      type: Number,
      required: true,
    },
    height: {
      type: Number,
      default: 225,
    },
    width: {
      type: Number,
      default: 175,
    },
  },
  computed: {
    src() {
      let useSeries = this.series;
      if (useSeries === '能量') {
        useSeries = 'energy';
      }
      return `https://ptcg.litttlebear.tw/${useSeries}/${paddingToThree(
        this.id
      )}.jpg`;
    },
    imgStyle() {
      return {
        width: `${this.width}px`,
        height: `${this.height}px`,
      };
    },
  },
};
</script>

<style lang="scss" scoped></style>
