function arrayToBase64(bytes) {
  let binary = '';
  let len = bytes.byteLength;
  for (let i = 0; i < len; i++) {
    binary += String.fromCharCode(bytes[i]);
  }
  return window.btoa(binary);
}

function base64ToArray(base64) {
  const chars = window.atob(base64);
  const ret = new Uint8Array(chars.length);
  for (let i = 0; i < chars.length; i++) {
    ret[i] = chars[i].charCodeAt();
  }
  return ret;
}

export function cardsToCode(cardList) {
  const cardMap = {};
  cardList.forEach((card, idx) => {
    if (cardMap[card.series] === undefined) {
      cardMap[card.series] = [];
    }
    cardMap[card.series].push(card.id, card.num, idx);
  });
  const codes = [];
  Object.keys(cardMap).forEach(function(series) {
    const arr = new Uint8Array(cardMap[series].length);
    cardMap[series].forEach((val, idx) => {
      arr[idx] = val;
    });
    codes.push(series + '.' + arrayToBase64(arr));
  });
  return codes.join('-');
}

export function codeToCards(code) {
  const partials = code.split('-');
  const cards = [];
  const tmp = {};
  let max = 0;
  let isOldFormat = false;
  partials.forEach(function(partial) {
    const base64 = partial.split('.')[1];
    const arr = base64ToArray(base64);

    if (arr.length % 3 !== 0) {
      isOldFormat = true;
    }
    if (!isOldFormat) {
      for (let i = 0; i < arr.length; i += 3) {
        if (tmp[arr[i + 2]]) {
          isOldFormat = true;
          break;
        }
        max = Math.max(max, arr[i + 2]);
        tmp[arr[i + 2]] = true;
      }
    }
  });
  if (Object.keys(tmp).length !== max + 1) {
    isOldFormat = true;
  }

  partials.forEach((partial) => {
    const [series, base64] = partial.split('.');
    const arr = base64ToArray(base64);
    if (isOldFormat) {
      for (let i = 0; i < arr.length; i += 2) {
        cards.push({
          series,
          id: arr[i],
          num: arr[i + 1],
        });
      }
    } else {
      for (let i = 0; i < arr.length; i += 3) {
        cards[arr[i + 2]] = {
          series,
          id: arr[i],
          num: arr[i + 1],
        };
      }
    }
  });

  return cards;
}
