<template lang="pug">
#app
  .title-row
    .title PTCG 卡表生成器
    .links(@click="openLINE")
      div.mr-r-4 卡片搜尋機器人
      img(src='./assets/line.png')
  .block
    .title 卡片搜尋
    filters(@add="addCard")
  .block
    .operations
      text-button.mr-r-12(@click="generateCardImg") 生成卡表
      text-button(@click="generateShareURL") 取得分享網址
  .block.card-list
    .title 卡片列表
    .list
      card.mr-r-20.mr-b-20(v-for="(card, idx) in cardList",
        :count="card.num",
        :series="card.series",
        :id="card.id", :key="idx",
        show-name, show-info)
  .block.ad
    InArticleAdsense(
      ins-class="adsbygoogle"
      data-ad-format="fluid"
      data-ad-client="ca-pub-3589478541680972"
      data-ad-slot="2925330139"
    )
</template>

<script>
import Card from '@/components/Card.vue';
import Filters from '@/components/Filters.vue';
import TextButton from '@/components/TextButton.vue';
import { cardsToCode, codeToCards } from '@/utils/code';
import { copyToClipboard } from '@/utils/util.js';

const numLimit = {
  pokemon: 4,
  trainer: 4,
  energy: 60,
};

export default {
  name: 'App',
  components: {
    Card,
    Filters,
    TextButton,
  },
  data() {
    return {
      cardList: [],
    };
  },
  methods: {
    openLINE() {
      window.open('https://line.me/R/ti/p/%40295kaagd', '_blank');
    },
    addCard(e) {
      const cardIdx = this.cardList.findIndex(
        (x) => x.series === e.selected.series && x.id === e.selected.id
      );
      if (cardIdx < 0) {
        this.cardList.push({
          num: e.num,
          ...e.selected,
        });
        return;
      }
      if (this.cardList[cardIdx].num < numLimit[this.cardList[cardIdx].type]) {
        this.cardList[cardIdx].num += 1;
      }
    },
    generateCardImg() {
      const code = cardsToCode(this.cardList);

      var request = new XMLHttpRequest();

      function loadData() {
        window.open(this.responseText, '_blank');
      }
      request.addEventListener('load', loadData);
      request.open(
        'POST',
        'https://line-bot.litttlebear.tw/generate?code=' + code,
        true
      );
      request.setRequestHeader(
        'Content-Type',
        'application/x-www-form-urlencoded; charset=UTF-8'
      );
      request.send({});
    },
    generateShareURL() {
      const code = cardsToCode(this.cardList);
      const url = new URL(window.location.href);
      const shareURL = url.origin + url.pathname + '?code=' + code;
      copyToClipboard(shareURL);

      alert('分享網址 ' + shareURL + ' 已經複製');
    },
  },
  mounted() {
    const url = new URL(document.location.href);
    const code = url.searchParams.get('code');
    if (code === '' || code === undefined || code === null) {
      return;
    }
    this.cardList = codeToCards(code);

    (window.adsbygoogle || []).push({});
  },
};
</script>

