<template lang="pug">
.card
  card-img(:series="series", :id="id")
  .count(v-if="showCount") {{count}}
  .info(v-if="showInfo") {{series}}-{{idStr}}
  .name(v-if="showName") {{name}}
</template>

<script>
import name from '@/name'
import { paddingToThree } from '@/utils/format.js';
import CardImg from './CardImg'

export default {
  name: 'card',
  components: {
    CardImg
  },
  props: {
    count: {
      type: Number,
      default: 1,
    },
    series: {
      type: String,
      required: true,
    },
    id: {
      type: Number,
      required: true,
    },
    showCount: {
      type: Boolean,
      default: true,
    },
    showName: {
      type: Boolean,
      default: false
    },
    showInfo: {
      type: Boolean,
      default: false
    },
  },
  data() {
    return {
    }
  },
  computed: {
    name() {
      if (!name[this.series]) {
        return ""
      }
      if (!name[this.series][this.id-1]) {
        return ""
      }
      return name[this.series][this.id-1].name
    },
    idStr() {
      return paddingToThree(this.id)
    }
  },
}
</script>

<style lang="scss" scoped>
.card {
  display: flex;
  flex-direction: column;
  align-items: center;
  .count {
    flex: 0 0 40px;
    width: 40px;
    margin-top: -30px;
    font-size: 20px;
    font-weight: bold;
    color: white;
    background: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .name {
    margin-top: 8px;
  }
  .info {
    margin-top: 8px;
  }
}
</style>