import { AC1a, AC1b, AC1D } from './nameAC1';
import { AC2a, AC2b, AC2D } from './nameAC2';
import { AS5a, AS5b } from './nameAS5';
import { AS6a, AS6b } from './nameAS6';
import { SC1a, SC1b, SC1D } from './nameSC1';
import { energy } from './nameEnergy';
import { pokemonProperties, trainerProperties } from './properties';

const datas = {
  AC1a,
  AC1b,
  AC1D,
  AC2a,
  AC2b,
  AC2D,
  AS5a,
  AS5b,
  AS6a,
  AS6b,
  SC1a,
  SC1b,
  SC1D,
  能量: energy,
};

const ret = {};
const nameMap = {};

Object.keys(datas).forEach((series) => {
  const data = datas[series];
  ret[series] = data.map((d, idx) => {
    const t = {};
    t.name = d[0];
    t.property = d[1];
    t.series = series;
    t.id = idx + 1;
    if (pokemonProperties.indexOf(t.property) >= 0) {
      t.type = 'pokemon';
    } else if (trainerProperties.indexOf(t.property) >= 0) {
      t.type = 'trainer';
    } else {
      t.type = 'energy';
    }

    if (nameMap[t.name]) {
      nameMap[t.name].push(t);
    } else {
      nameMap[t.name] = [t];
    }

    return t;
  });
});

function search(type, property, partialName) {
  let nameList = Object.keys(nameMap);
  if (partialName !== '') {
    nameList = nameList.filter((name) => name.indexOf(partialName) >= 0);
  }

  const ret = nameList.reduce((r, d) => {
    r.push(
      ...nameMap[d].filter((data) => {
        return (
          (data.type === type || type === '') &&
          (data.property === property || property === '')
        );
      })
    );
    return r;
  }, []);

  ret.sort((x, y) => {
    if (x.series === y.series) {
      return x.id > y.id ? 1 : -1;
    }
    return x.series > y.series ? 1 : -1;
  });
  return ret;
}

ret.search = search;

export default ret;
